import { Component, ViewChild, ElementRef, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';

declare var google;
@IonicPage()
@Component({
  selector: 'page-select-location-modal',
  templateUrl: 'select-location-modal.html',
})
export class SelectLocationModalPage {
  @ViewChild("map") mapElement: ElementRef;
  GoogleAutoComplete:any;
  autoComplete:any;
  acItems:any;
  map: any;
  markers: any;
  geocoder: any;


  constructor(public navCtrl: NavController, private view: ViewController, public navParams: NavParams,  private vCtrl: ViewController, private zone: NgZone) {
    this.GoogleAutoComplete = new google.maps.places.AutocompleteService();
    this.autoComplete = { input: '' };
    this.acItems = [];
    this.geocoder = new google.maps.Geocoder;
    this.markers = [];
  }

  updateResults(){
    if (this.autoComplete.input == '') {
      this.acItems = [];
      return;
    }
    this.GoogleAutoComplete.getPlacePredictions({ input: this.autoComplete.input },
    (predictions) => {
      this.acItems = [];
      this.zone.run(() => {
        predictions.forEach((prediction) => {
          this.acItems.push(prediction);
        });
      });
    });
   }

 async selectSearchResult(item){
    this.acItems = [];
    this.geocoder.geocode({'placeId': item.place_id}, async (results, status) => {
      if(status === 'OK' && results[0]){
        let position = {
            lat: results[0].geometry.location.lat(),
            lng: results[0].geometry.location.lng()
        };
        let marker = new google.maps.Marker({
          position: results[0].geometry.location,
          map: this.map,
        });
      await  this.markers.push(marker);
        this.map.setCenter(results[0].geometry.location);
        var venue = this.autoComplete.input.toString().toUpperCase();
        console.log(position.lat, position.lng, venue);
        var venueData = []
        venueData.push(venue)
        var latData = []
        latData.push(position.lat)
        var lngData = []
        lngData.push(position.lng)
        this.view.dismiss({venueData, latData, lngData});
      }
    })
  }
 
  ionViewDidLoad() {
    console.log('ionViewDidLoad SelectLocationModalPage');
    this.loadMap();
  }

close(){
  this.vCtrl.dismiss();

}

loadMap() {
  var uluru = {lat: -25.344, lng: 131.036};
      let mapOptions = {
        center: uluru,
        zoom: 1,
        zoomControl: true,
        disableDefaultUI: true,
        mapTypeId: google.maps.MapTypeId.ROADMAP
      };
      this.map = new google.maps.Map(
        this.mapElement.nativeElement,
        mapOptions
      );

      }
    }



